import { useCallback } from 'react';

import ConnectInterface from '../interfaces/connect.interface';
import useOptions from './use-options';

const DEFAULT_OPTIONS = {
  Accept: 'application/json, */*;q=0.1',
};

const STATIC_SNAP_BASE_URL = 'http://localhost:4321/api';

const useStaticSnapAPI = (action: string, requestOptions: RequestInit) => {
  const { getOptions } = useOptions<ConnectInterface>('connect');
  const request = useCallback(
    /**
     * Make a request to the Static Snap API
     * @param callbackOptions RequestInit options
     * @param requestAction string to append to the action
     * @returns
     */
    async (callbackOptions: RequestInit = {}, requestAction = '') => {
      const options = await getOptions();

      const fetchOptions = {
        ...DEFAULT_OPTIONS,
        ...requestOptions,
        ...callbackOptions,
      };
      fetchOptions.headers = {
        ...fetchOptions.headers,
        Authorization: `Bearer ${options?.installation_access_token}`,
      };
      return fetch(`${STATIC_SNAP_BASE_URL}${action}${requestAction}`, fetchOptions);
    },
    [action, requestOptions, getOptions]
  );

  return { request };
};

export default useStaticSnapAPI;
